import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';


const OneBlogPage = () => {

    const { id } = useParams();
    const [post, setPost] = useState(null)

    useEffect(() => {
        (async () => {
            const { data } = await axios(`https://ajax.test-danit.com/api/json/posts/${id}`)
            console.log(data)

            setPost(data);
        })()
    }, [])

    return (

    <section>
        <h1>Hello from OneBlogPage</h1>

        <h2>{post?.title}</h2>
        <p>{post?.body}</p>
    </section>
    )
}

export default OneBlogPage;